import './bus-arrival-container.scss';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import BuchiBreadcrumbs from '../../../component/BuchiBreadcrumbs/buchi-breadcrumbs';
import BuchiChecklist from '../../../component/BuchiChecklist/buchi-checklist';
import BuchiImage from '../../../component/BuchiImage/buchi-image';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import commerce from '../../../lib/commerce';
import lodash from 'lodash';
import moment from 'moment';
import {
  Button,
  Pagination,
  PaginationItem,
  Stack,
} from '@mui/material';
import { Grid } from '@material-ui/core';
import { useLocation, useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useWindowDimension } from '../../../hooks/bubuchichis-hook';

const testFn = () => {
  return axios.get('http://datamall2.mytransport.sg/ltaodataservice/BusArrivalv2', {
    headers: {
      'Access-Control-Allow-Origin': '*',
      Accept: 'application/json',
      AccountKey: 'TiI4w3aoTSCDW1l4TfAykw==',
    },
    params: {
      BusStopCode: 83139,
    },
  })
  .then(response => {
    // eslint-disable-next-line
    console.log(response);
  })
  .catch(error => {
    // eslint-disable-next-line
    console.log(error);
  });
};

const BusArrivalContainer = ({ onSetIsLoaded }) => {
  const [lastUpdated, setLastUpdated] = useState('');
  const t = useTranslation()[0];
  const breadcrumbs = [
    {
      name: 'homePage',
      label: t('navBar.homePage'),
      path: '/',
    },
    {
      name: 'busArrivalPage',
      label: t('navBar.busArrivalPage'),
      path: '/bus',
    },
  ];

  useEffect(() => {
    axios.get('http://datamall2.mytransport.sg/ltaodataservice/BusArrivalv2?BusStopCode=83139', {
      headers: {
        'Access-Control-Allow-Origin': '*',
        Accept: 'application/json',
        AccountKey: 'TiI4w3aoTSCDW1l4TfAykw==',
      },
    })
    .then(response => {
      // eslint-disable-next-line
      console.log(response);
    })
    .catch(error => {
      // eslint-disable-next-line
      console.log(error);
    });
    onSetIsLoaded(true);
  }, []);

  return (
    <Grid container className="page-width">
      <Grid item xs={12}>
        <BuchiBreadcrumbs breadcrumbs={breadcrumbs} />
      </Grid>
      <Grid item xs={12}>
        <h1 className="page-title">Bus Arrival</h1>
        <p>last updated: {}</p>
      </Grid>
      <Grid item xs={12}>
        <Button onClick={() => testFn()} className="site-header__icon site-header__darkmode">
          test
        </Button>
        <h2>Stadium MRT:</h2>
        <h4>11:</h4>
        <p>3 mins - towards Tanjong Rhu</p>
        <p>5 mins - towards Kallang MRT</p>
      </Grid>
    </Grid>
  );
};

BusArrivalContainer.propTypes = {
  onSetIsLoaded: PropTypes.func.isRequired,
};

export default BusArrivalContainer;
